import wPicker from "@/components/w-picker/w-picker.vue";

var wxCharts = require("../../../utils/wxcharts.js");
var _self;
var Charts;

var Data = {
	categories: ['9', '10', '11', '12', '13', '14', '15','16','17'],
	series: [{
			name: 'AQI',
			data: [63, 60, 65, 58, 51, 54, 57,63, 67]
		}
	]
};
var width;

export default {
	components:{
		wPicker
	},
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: this.getNowFormatDate()+'市空气监控'
		});
		
		uni.getSystemInfo({
			success(res) {
				width = res.screenWidth - 10;
			}
		})
	},
	data() {
		return {
            title: 'Hello',
			sdate:this.getNowFormatDate(),
			tabList:[{
				mode:"date",
				name:"日期选择",
				value:[this.getNowYear(),this.getNowMonth(),this.getNowDay()] //年月日在列表的序号
			}],
			tabIndex:0
		}
	},
	onReady: function() {
		this.ShowCharts("charts", Data);
		//this.hideLoading();
	},
	computed:{
		mode(){
			return this.tabList[this.tabIndex].mode
		},
		defaultVal(){
			return this.tabList[this.tabIndex].value
		}
	},
	methods: {
		toggleTab(index){
			this.tabIndex=index;
			this.$refs.picker.show();
		},
		onConfirm(val){
			//当前所选择的日期
			this.sdate = val.result;
			console.log(val.result);
			
			uni.setNavigationBarTitle({
			title: this.sdate+'市空气监控'
		   });
		},
		/*显示图表*/
		ShowCharts: function(canvasId, data) {
			Charts = new wxCharts({
				canvasId: canvasId,
				type: 'line',
				legend: true,
				fontSize: 11,
				background: '#FFFFFF',
				animation: true,
				categories: data.categories,
				series: data.series,
				width: width,
				height: 280,
				pixelRatio:1,
			});
		},
		
		goDetail:function(id,storeName){			
			let detail = {
					id: id,
					storeName:storeName,
					date:this.sdate
				}
				uni.navigateTo({
					url: "hourdata02?detail=" + encodeURIComponent(JSON.stringify(detail))
				})
		},
		
		getNowFormatDate: function() {
			var date = new Date();
			var seperator1 = "-";
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			var strDate = date.getDate() ;
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			if (strDate >= 0 && strDate <= 9) {
				strDate = "0" + strDate;
			}
			var currentdate = year + seperator1 + month + seperator1 + strDate;
			return currentdate;
		},
		
		getNowYear: function() {
			var date = new Date();
			var year = date.getFullYear();
			var currentdate = year -2018 ;
			return currentdate;
		},
		
		getNowMonth: function() {
			var date = new Date();
			var month = date.getMonth() + 1;
			var currentdate = month - 1;
			return currentdate;
		},
		
		getNowDay: function() {
			var date = new Date();
			var strDate = date.getDate() ;
			var currentdate = strDate - 1;
			return currentdate;
		},
		
	}
}
